html {
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  padding: 0 50px;
  display: flex;
}

.logo__link {
  align-items: center;
  display: flex;
}

.nav__list {
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__item {
  margin-left: 20px;
  list-style-type: none;
}

.nav__link {
  text-transform: uppercase;
  color: #000;
  align-items: center;
  height: 60px;
  font-size: 12px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 10px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}
